@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  height: 100%;
  width: 100%;
  overflow-x: hidden;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    background-color: #1a2c38;
}

select, textarea, input[type] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.canvasCon > canvas {
  width: 100%;
}

.betButton {

}

.betButton:not(:disabled):active {
  scale: 0.98;
}

.history-table {

}

.history-table > tbody tr:nth-child(odd) {
  background: #1a2c38;
}

.thinScrollbar {
  scrollbar-width: thin;
  scrollbar-color: #2f4553 transparent;
}